import { Box, IconButton, Stack, Tooltip, Typography } from "@mui/material";
import { display } from "@mui/system";
import meta from "../../../images/meta.png";
import React from "react";
import { ProfileHeader } from "./profileHeader";
import ContentCopyIcon from "@mui/icons-material/ContentCopy";
import ProfileTabs from "./profileTabs";
import FacebookIcon from "@mui/icons-material/Facebook";
import InstagramIcon from "@mui/icons-material/Instagram";
import TwitterIcon from "@mui/icons-material/Twitter";
import LinkedInIcon from "@mui/icons-material/LinkedIn";
import { useSelector } from "react-redux";
const Profile = () => {
  const { ceilSlice } = useSelector((state) => state);
  const { userdetails } = ceilSlice;

  const array = [
    {
      name: "Facebook",
      link: userdetails?.facebook,
      icon: <FacebookIcon />,
      id: 1,
    },
    {
      name: "Instagram",
      link: userdetails?.instagram,
      icon: <InstagramIcon />,
      id: 2,
    },
    {
      name: "Twitter",
      link: userdetails?.twitter,
      icon: <TwitterIcon />,
      id: 3,
    },
    {
      name: "LinkedIn",
      link: userdetails?.linkdin,
      icon: <LinkedInIcon />,
      id: 4,
    },
  ];

  return (
    <>
      <Box
        sx={{
          width: "100%",
          minHeight: "100vh",
          alignItems: "start",
          display: "flex",
          justifyContent: "space-between",
        
        }}
      >
        <Box
          sx={{
            width: { xs: "100%", md: "100%" },
            display: "flex",
            flexDirection: "column",
            fontSize: "70px",
          }}
        >
          <ProfileHeader />
          <Box
            sx={{
              width: "96%",
              mx: "auto",

              display: "flex",
              flexDirection: { xs: "column", md: "row" },
            }}
          >
            <Box
              sx={{
                width: {lg: "20%" },
                maxWidth:"375px", mx:"auto"
              }}
            >
              <Stack spacing={2} sx={{ textAlign: "center" }}>
                <Typography
                  sx={{
                    fontSize: { xs: "15px", md: "25px", fontWeight: "bold" },
                  }}
                >
                  Metacubez
                </Typography>
                <Typography variant="body2" sx={{ color: "text.secondary" }}>
                  Joined Feb 2023{" "}
                </Typography>
                <Box
                  sx={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                  }}
                >
                  <img
                    src={meta}
                    alt="meta"
                    srcset={meta}
                    style={{ width: "30px", height: "30px" }}
                  />
                  <Typography
                    sx={{ maxWidth: "200px",fontWeight:"bold", overflow: "hidden", ml: 1 }}
                  >
                    azwe90281ij...i90
                  </Typography>
                  <IconButton>
                    <ContentCopyIcon />
                  </IconButton>
                </Box>
                <Typography variant="body2" sx={{ color: "text.secondary" , }}>
                  Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed
                  do eiusmod tempor incididunt ut labore et dolore magna aliqua.
                  Ut enim ad minim veniam, quis nostrud
                </Typography>
                <Typography>contact@Metacubez.com</Typography>

                <Box
                  sx={{
                    // color: "white",
                    fontSize: "20px",
                  }}
                >
                  {array.map((item, index) => {
                    return (
                      <Tooltip title={item.name} placement="top" key={item?.id}>
                        <a href={item.link} target="_blank">
                          <IconButton
                            fontSize="small"
                            sx={{
                              background:
                                "linear-gradient(90.1deg, #4F98D0 0.11%, #34D9B1 95.94%)",
                              ml: 1,
                              mb: 1,
                              borderRadius: "50%",
                              width: "35px",
                              height: "35px",
                              // color: "white",
                            }}
                          >
                            {item.icon}
                          </IconButton>
                        </a>
                      </Tooltip>
                    );
                  })}
                </Box>
              </Stack>
            </Box>
           

            <ProfileTabs />
          </Box>
        </Box>
      </Box>
    </>
  );
};

export default Profile;
